'use client'
import React from 'react'
import { VentaServicio } from '../../../entities/servicios'

interface Props {
  reports: VentaServicio[]
  technicianOptions: { idUsuario: number; nombre: string }[]
  formatCurrency: (value: number) => string
  formatDate: (iso: string) => string
  exportToCSV: (venta: VentaServicio) => void
  viewReportDetails: (venta: VentaServicio) => void
}

export default function ReportsTable({
  reports = [],
  technicianOptions,
  formatCurrency,
  formatDate,
  exportToCSV,
  viewReportDetails
}: Props) {
  return (
    <div className="products-table-container">
      <table className="products-table">
        <thead>
          <tr>
            <th>ID Venta</th>
            <th>Cliente</th>
            <th>T√©cnico</th>
            <th>Servicios</th>
            <th>Productos</th>
            <th>Descuento</th>
            <th>Costo Total</th>
            <th>Fecha</th>
            <th>Acciones</th>
          </tr>
        </thead>
        <tbody>
          {reports.map((venta) => (
            <tr key={venta.idVentaServicio}>
              <td>{venta.idVentaServicio}</td>
              <td>{venta.cliente.nombre}</td>
              <td>{venta.tecnico.nombre}</td>
              <td>
                {venta.detallesServicio.map(ds => (
                  <div key={ds.idDetalleServicio}>
                    {ds.servicio.nombre} (S/ {ds.costoTotal})
                  </div>
                ))}
              </td>
              <td>
                {venta.productos.map(p => (
                  <div key={p.idProductoDetalleVenta}>
                    {p.producto.nombre} x{p.cantidad} (S/ {p.precioTotal})
                  </div>
                ))}
              </td>
              <td>{venta.descuento ? `S/ ${venta.descuento}` : '‚Äî'}</td>
              <td>{formatCurrency(venta.costoTotal)}</td>
              <td>{formatDate(venta.fechaRegistro)}</td>
              <td>
                <button className="action-btn view" onClick={() => viewReportDetails(venta)}>Ver</button>
                <button className="action-btn export" onClick={() => exportToCSV(venta)}>Exportar</button>
              </td>
            </tr>
          ))}
          {reports.length === 0 && (
            <tr>
              <td colSpan={9} className="text-center">No hay reportes generados</td>
            </tr>
          )}
        </tbody>
      </table>
    </div>
  )
}
