'use client'
import React from 'react'
import ReportFilters from '../../components/servicios/ReportFilters'
import ReportsTable from '../../components/servicios/ReportsTable'
import ReportModal from '../../components/servicios/ReportModal'
import ServiceForm from '../../components/servicios/ServiceForm'
import ServicesTable from '../../components/servicios/ServicesTable'
import useGestionServicios from '../../../features/useGestionServicios'
import '../../../styles/servicios.css'

export default function GestionServiciosPage() {
  const {
    activeTab,
    setActiveTab,
    filters,
    setFilters,
    searchTerm,
    setSearchTerm,
    ventasRegistradas,
    reports,
    selectedReport,
    setSelectedReport,
    technicianOptions,
    generateReport,
    clearFilters,
    isGeneratingReport,
    exportToCSV,
    viewReportDetails,
    formatCurrency,
    formatDate,
    registrarVenta
  } = useGestionServicios()

  return (
    <div className="dashboard-container">
      <h1 className="text-3xl font-semibold mb-6">üîß Gesti√≥n de Servicios</h1>

      <div className="tabs-container">
        <button
          className={`tab-btn ${activeTab === 'registrar' ? 'active' : ''}`}
          onClick={() => setActiveTab('registrar')}
        >
          üìù Registrar Servicio
        </button>
        <button
          className={`tab-btn ${activeTab === 'reportes' ? 'active' : ''}`}
          onClick={() => setActiveTab('reportes')}
        >
          üìä Reportes
        </button>
      </div>

      {activeTab === 'registrar' && (
        <>
          <section className="box-white">
            <ServiceForm onRegistrarVenta={registrarVenta} />
          </section>

          <section className="box-white mt-6">
            <div className="flex justify-between items-center mb-4">
              <h2 className="section-title">Historial de Servicios</h2>
              <input
                type="text"
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                className="search-input"
                placeholder="üîç Buscar por cliente..."
              />
            </div>
            <ServicesTable
              ventas={ventasRegistradas}
              searchTerm={searchTerm}
              onExport={exportToCSV}
              onView={viewReportDetails}
            />
          </section>
        </>
      )}

      {activeTab === 'reportes' && (
        <>
          <section className="box-white">
            <h2 className="section-title">Filtros para Reporte</h2>
            <ReportFilters
              filters={filters}
              setFilters={setFilters}
              technicianOptions={technicianOptions}
            />
            <div className="action-buttons mt-4">
              <button
                onClick={generateReport}
                className="primary-btn"
                disabled={isGeneratingReport}
              >
                üìÑ {isGeneratingReport ? 'Generando...' : 'Generar Reporte'}
              </button>
              <button onClick={clearFilters} className="filter-btn">
                üßπ Limpiar
              </button>
            </div>
          </section>

          <section className="box-white mt-6">
            <h2 className="section-title">Reportes Generados</h2>
            <ReportsTable
              reports={reports}
              technicianOptions={technicianOptions}
              formatCurrency={formatCurrency}
              formatDate={formatDate}
              exportToCSV={exportToCSV}
              viewReportDetails={viewReportDetails}
            />
          </section>

          {selectedReport && (
            <ReportModal
              selectedReport={selectedReport}
              setSelectedReport={setSelectedReport}
              formatCurrency={formatCurrency}
              formatDate={formatDate}
              exportToCSV={exportToCSV}
            />
          )}
        </>
      )}
    </div>
  )
}
